import axios from 'axios'
import qs from 'qs'

let baseurl = ''
if(window.location.host.indexOf('localhost') != -1){
  baseurl = '/api'
} else{
  baseurl = window.location.protocol+"//"+ window.location.host
}

export default {
  wxShowMenu: function(data) {
    let parmars = {
      act: 'getJsSignature',
      url: encodeURIComponent(window.location.href)
    };
    axios.post(
      baseurl + '/activity/mobileapi/do.php',
      qs.stringify(parmars)
    ).then(function(res) {
      var getMsg = res.data.signature
      wx.config({
        debug: false, //生产环境需要关闭debug模式
        appId: getMsg.appId, //appId通过微信服务号后台查看
        timestamp: getMsg.timestamp, //生成签名的时间戳
        nonceStr: getMsg.nonceStr, //生成签名的随机字符串
        signature: getMsg.signature, //签名
        jsApiList: [ //需要调用的JS接口列表
          'onMenuShareTimeline', //分享给好友
          'onMenuShareAppMessage', //分享到朋友圈
          'getLocation'
        ]
      });
      wx.ready(function() {
        // 分享到朋友圈
        wx.onMenuShareTimeline({
          title: data.titles, // 分享标题
          desc: data.descs, //分享描述
          link: data.link, // 分享链接
          imgUrl: 'https://wx.boluoyunyu.com/bjchs/img/logo.jpg' // 分享图标
        });
        //分享给朋友
        wx.onMenuShareAppMessage({
          title: data.titles, // 分享标题
          desc: data.descs, //分享描述
          link: data.link, // 分享链接
          imgUrl: 'https://wx.boluoyunyu.com/bjchs/img/logo.jpg' // 分享图标
        });
      });
    })
  }

}



