import { POST } from '../server/fetch'

let baseurl = ''
if(window.location.host.indexOf('localhost') != -1){
  baseurl = '/api'
} else{
  baseurl = window.location.protocol+"//"+ window.location.host
}

// 每个页面判断是否登录，是否是黑名单
export const iflogo = (data) => POST(baseurl + '/activity/mobileapi/do.php', data)
export const ifhei = (data) => POST(baseurl + '/activity/mobileapi/do.php', data)

// 医院攻略列表
export const hospital = (data) => POST(baseurl + '/activity/mobileapi/publichos/hoslist.php', data)
// 医院攻略详情
export const hosdel = (data) => POST(baseurl + '/activity/mobileapi/publichos/hosdel.php', data)

// 建档经验
export const archiveExperience = (data) => POST(baseurl + '/activity/mobileapi/publichos/archiveExperience.php', data)
// 产检经验
export const inspectionExperience = (data) => POST(baseurl + '/activity/mobileapi/publichos/inspectionExperience.php', data)
// 分娩经验
export const deliverExperience = (data) => POST(baseurl + '/activity/mobileapi/publichos/deliverExperience.php', data)
// 其他经验
export const otherExperience = (data) => POST(baseurl + '/activity/mobileapi/publichos/otherExperience.php', data)

// 加菠萝姐
export const addboluojie = (data) => POST(baseurl + '/activity/mobileapi/addboluojie/addboluojie.php', data)



